* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(231, 182, 174);
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.app-wrapper {
  width: 300px;
  padding: 10px;
  background: #b32c2c;
  color: #fff;
  font-family: 'Courier New', Courier, monospace;
  border-radius: 2px;
  box-shadow: 1px 1px 5px #c8aaaf;
}

.title {
  margin: 20px auto;
  text-transform: uppercase;
}

.text-center {
  text-align: center;
}

.ie-balance {
  background-color: #5009322b;
  padding: 5px 0;
}

.inc-exp-container {
  margin: 10px auto;
  display: flex;
  flex-direction: row;
  background-color:  #15151694;;
  padding: 20px;
  justify-content: space-around;
  box-shadow: 2px 2px 5px 1px #0005;
}



.inc-exp-container p {
  text-align: center;
}

.list {
  list-style-type: none;
  margin: 0;

}

.list li {
  color: #000;
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
  position: relative;
  padding: 5px 15px;
  background-color: #fff;
}

.delete-btn {
  display: none;
  position: absolute;
  left: 2px;
  top: 5px;
  font-size: 18px;
  color: rgb(160, 54, 54);
  border: none;
  box-shadow: 0 0 0 red;
  transition: box-shadow 1s ease;
  cursor: pointer;
}

.delete-btn:hover {
  box-shadow: 1px 1px 1px  rgb(204, 113, 113);;
}

.delete-btn:focus {
  outline: none;
}

.list li:hover .delete-btn {
  display: block;
}

.add-newtransaction {
  margin-top: 20px;
  text-decoration: underline;
}

.form-control label {
  display: block;
  margin: 15px 0 0px;
}

.form-control input {
  display: block;
  width: 100%;
  border: none;
  padding: 5px;
  box-shadow: 1px 1px 5px #0004;
}

.form-control input:focus {
  outline: none;
}

form button {
  display: block;
  width: 100%;
  margin: 10px 0;
  background-color: rgb(216 76 19 / 43%);
  border: none;
  padding: 8px 0;
  color: #fff;
  font-weight: 500;
  outline: none;
  cursor: pointer;
  border-radius: 20px;
  border: 2px solid transparent;
}

form button:focus {
  box-shadow: 1px 1px 5px #15141454;
}

.inc-exp-container h4 {
  text-transform: uppercase;
}

.money {
  font-weight: bold;
}

.plus {
  color: rgb(20, 199, 130);
}

.minus {
  color: rgb(226, 57, 57);
}

li.minus {
  border-right: 3px solid rgb(226, 57, 57);
}

.ie-balance {
  color: #b2b2ff;
}



li.plus {
  border-right:3px solid rgb(20, 199, 130);
}

